'use client';

import React, { useEffect, useState } from 'react';
import { fetchCryptoData, fetchCryptoOhlcv } from '@/services/services';
import { CryptoOhlcvStats, CryptoPrice } from '@/app/types';
import styles from './CryptoCard.module.scss';

interface Props {
  symbol: string;
}

const CryptoCard: React.FC<Props> = ({ symbol }) => {
  const [logo, setLogo] = useState<string>('');
  const [price, setPrice] = useState<number | null>(null);
  const [percentChange24h, setPercentChange24h] = useState<number | null>(null);
  const [ohlcv, setOhlcv] = useState<CryptoOhlcvStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [coinName, setCoinName] = useState<string>('');

  useEffect(() => {
    const loadData = async () => {
      setLoading(true);
      try {
        const listingsData = await fetchCryptoData('listings');
        const coin: CryptoPrice | undefined = listingsData.data.find((c: CryptoPrice) => c.symbol === symbol);

        if (!coin) {
          console.warn(`Symbol "${symbol}" iÃ§in coin bulunamadÄ±.`);
          return;
        }

        setPrice(coin.quote.USD.price);
        setPercentChange24h(coin.quote.USD.percent_change_24h);
        setCoinName(coin.name);

        const infoData = await fetchCryptoData('info', [coin.id.toString()]);
        const logoUrl = infoData.data?.[coin.id]?.logo || '';
        setLogo(logoUrl);

        const ohlcvData = await fetchCryptoOhlcv(coin.id.toString());
        setOhlcv(ohlcvData);

      } catch (error) {
        console.error("Veri Ã§ekme hatasÄ±:", error);
      } finally {
        setLoading(false);
      }
    };

    loadData();
  }, [symbol]);

  if (loading) return <div className="p-4 border rounded-xl shadow-md">YÃ¼kleniyor...</div>;
  if (!ohlcv || price === null || percentChange24h === null) return <div className="p-4 border rounded-xl shadow-md text-red-500">Veri yÃ¼klenemedi.</div>;

  return (
    <div className="bg-white shadow-md rounded-xl p-4 w-full max-w-sm border">
      <div className="flex items-center gap-3 mb-2">
        {logo && <img src={logo} alt={symbol} className="w-8 h-8" />}
        <h2 className="text-xl font-bold">{coinName} ({symbol})</h2>
      </div>
      <div className="space-y-1 text-gray-700 text-sm">
        <p>ðŸ’° <strong>Last Price:</strong> ${price.toFixed(2)}</p>
        <p>ðŸ“ˆ <strong>24h Change:</strong> {percentChange24h.toFixed(2)}%</p>
        <p>ðŸ”º <strong>High:</strong> ${ohlcv.high.toFixed(2)}</p>
        <p>ðŸ”» <strong>Low:</strong> ${ohlcv.low.toFixed(2)}</p>
        <p>ðŸ“Š <strong>Volume 24h:</strong> ${ohlcv.volume24h.toLocaleString()}</p>
      </div>
    </div>
  );
};

export default CryptoCard;
